@font-face {
    font-family: 'AclueFont';
    src: url('assets/Garet-Book.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

html, body {
    height: 100%;
    max-height: 100%;
    margin: 0;
    padding: 0;
}

/* Ensure the root element occupies the full height */
#root {
    height: 100%;
    max-height: 100%;
    display: flex;
    flex-direction: column;
}

.scroll_box {
    overflow: auto;
    /* WebKit browser scrollbar styling (Chrome, Safari, Edge) */
    scrollbar-width: thin; /* For Firefox */
    scrollbar-color: #888 #f1f1f1; /* For Firefox (thumb and track colors) */

}